.home_container {
    color: black;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    text-align: center;
    align-self: center;
    height: 82vh;
    margin-bottom: 10vh;
}

.home_image {
    background-size: cover;
    background-position: center;
    height: 100%;
}

.home_imageText1 {
    color: black;
    display: flex;
    font-size: 3vmax;
    font-weight: 600;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: rgb(255, 255, 255, 0.5);
    width: 100%;
    padding: 1vw;
    height: 20vh;
}

.home_imageText2 {
    color: black;
    display: flex;
    font-size: 2vmax;
    font-weight: 600;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: rgb(255, 255, 255, 0.5);
    padding: 1vw;
    height: 20vh;
}

.home_imageButton {
    background-color: transparent;
    border: 1px solid black;
    border-radius: 10px;
    padding: 1.5vh 2vw;
    margin-top: 1vh;
    cursor: pointer;
}

.home_imageButton:hover {
    background-color: #232f3e;
    color: white;
}

.home_filterButton {
    background-color: #232f3e;
    color: white;
    border-radius: 10px;
    padding: 1.5vh 2vw;
    margin-top: 1vh;
    cursor: pointer;
}

.home_filterButton:hover {
    background-color: #1b2531;
    color: white;
}

.home_imageP {
    opacity: 1;
}

